import React, { useEffect, useState } from 'react';
import { Button, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ numSeries, pNumReps, editable, size, pWeigth }) => {
  const [series, setSeries] = useState(Array.from(Array(numSeries).keys()));
  const [isEditable, setIsEditable] = useState(editable);
  const [weight, setWeigth] = useState(pWeigth);
  const [numReps, setNumReps] = useState(pNumReps);
  const [title, setTitle] = useState(size);

  useEffect(() => {
    setIsEditable(editable);
    console.log(pNumSeries);
    setSeries(Array.from(Array(pNumSeries).keys()));
    console.log(series);
    setWeigth(pWeigth);
    setNumReps(pNumReps);
  }, [editable, pNumSeries, pNumReps, pWeigth, size]);

  const handleOnSave = () => {
    alert('Saved!!');
  };

  return (
    <Container>
      <Row>
        <h2>This is a {title} size serie</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              <th>#</th>
              <th>#repetitions</th>
              <th>weight</th>
            </tr>
          </thead>
          <tbody>
            {series.map(r => (
              <Rep key={r} id={r} pNumReps={numReps} pWeigth={weight} editable={isEditable}></Rep>
            ))}
          </tbody>
        </Table>
        <Button onClick={handleOnSave}>Save</Button>
      </Row>
    </Container>
  );
};
